body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-open-sans: "Open Sans";
  --font-roboto-slab: "Roboto Slab";
  --font-playfair-display: "Playfair Display";

  /* font sizes */
  --font-size-sm-6: 13.6px;
  --font-size-7xs-1: 5.1px;
  --font-size-5xs-1: 7.1px;
  --font-size-xs-1: 11.1px;
  --font-size-7xs-8: 5.8px;
  --font-size-9xl: 28px;
  --font-size-5xs: 8px;
  --font-size-7xs-6: 5.6px;
  --font-size-7xs-3: 5.3px;
  --font-size-xs-2: 11.2px;
  --font-size-xs: 12px;

  /* Colors */
  --color-white: #fff;
  --offblck: #4e4e4e;
  --color-black: #000;
  --color-gainsboro-100: #d9d9d9;

  /* Border radiuses */
  --br-3xs: 10px;
}
